.openingContainerStart{
  background-color: #282c34;
  position: relative;
  z-index: 1101;
  animation-duration: 8s;
  animation-name: openingFadeOut;
  }
    @keyframes openingFadeOut {
        from {
            opacity: 1;
        }
        80% {
          opacity: 1;
          }
        to {
            opacity: 0;
        }
    }

.openingContainer{
    background-image: url("../images/header-img-full.jpg");
    background-repeat: no-repeat;
    background-position: bottom center;
    box-sizing: content-box;
    height: 100vh;
    background-size: cover;
    color: #e8cd33;
    animation-duration: 4s;
    animation-name: opening;
  }
    @keyframes opening {
        from {
            opacity: 0;
            z-index: 11001;
        }
        to {
            opacity: 1;
            z-index: 11001;
        }
    }
  
  /*opening letters and CSS animations*/
  @media (orientation: portrait) {
      .letterOpen1{
        position: absolute;
        top: 5%;
        left: 2%;
        writing-mode: vertical-rl;
        font-family: 'Varela Round', sans-serif;
        font-size: 6rem;
        animation-duration: 4s;
        animation-name: slidein1p;
        }
        @keyframes slidein1p {
        from {
          left: 70%;
          opacity: 0;
          }
        75% {
          left: 1%;
          }
        to {
          left: 2%;
          opacity: 1;
        }
        }
        .letterOpen2{
            position: absolute;
            top: 29%;
            left: 8%;
            font-family: 'Varela Round', sans-serif;
            font-size: 3.5rem;
            animation-duration: 4s;
            animation-name: slidein2p;
            }
            @keyframes slidein2p {
            from {
                top: 80%;
                opacity: 0;
                }
            75% {
                top: 28%;
                }
            to {
            top: 29%;
          opacity: 1;
        }
    }
    .letterOpen3{
      position: absolute;
      top: 43%;
      left: 30%;
      font-family: 'Varela Round', sans-serif;
      font-size: 2rem;
      animation-duration: 4s;
      animation-name: slidein3p;
    }
    @keyframes slidein3p {
      from {
          left: 0%;
          opacity: 0;
        }
        75% {
            left: 31%;
          }
        to {
          left: 30%;
          opacity: 1;
        }
    }
    .letterOpen4{
      position: absolute;
      top: 49%;
      left: 35%;
      font-family: 'Varela Round', sans-serif;
      font-size: 2rem;
      animation-duration: 4s;
      animation-name: slidein4l;
    }
    @keyframes slidein4l {
      from {
          top: 0%;
          opacity: 0;
        }
        75% {
            top: 50%;
          }
        to {
          top: 49%;
          opacity: 1;
        }
    }
  }
  @media (orientation: landscape){
  .letterOpen1{
    position: absolute;
    top: 4%;
    left: 1%;
    writing-mode: vertical-rl;
    font-family: 'Varela Round', sans-serif;
    font-size: 7rem;
    animation-duration: 4s;
    animation-name: slidein1l;
  }
  @keyframes slidein1l {
    from {
        left: 80%;
        opacity: 0;
      }
      
      75% {
        left: 0%;
      }
    
      to {
        left: 1%;
        opacity: 1;
      }
  }
  .letterOpen2{
    position: absolute;
    top: 27%;
    left: 16%;
    font-family: 'Varela Round', sans-serif;
    font-size: 3rem;
    animation-duration: 4s;
    animation-name: slidein2l;
  }
  @keyframes slidein2l {
    from {
        top: 80%;
        opacity: 0;
      }
      75% {
        top: 26%;
      }
      to {
        top: 27%;
        opacity: 1;
      }
  }
  .letterOpen3{
    position: absolute;
    top: 45%;
    left: 30%;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    animation-duration: 4s;
    animation-name: slidein3l;
  }
  @keyframes slidein3l {
    from {
        left: 0%;
        opacity: 0;
      }
      75% {
        left: 31%;
      }
      to {
        left: 30%;
        opacity: 1;
      }
  }
  .letterOpen4{
    position: absolute;
    top: 53%;
    left: 41%;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    animation-duration: 4s;
    animation-name: slidein4l;
  }
  @keyframes slidein4l {
    from {
        top: 0%;
        opacity: 0;
      }
      75% {
        top: 54%;
      }
      to {
        top: 53%;
        opacity: 1;
      }
  }
  }
