.aboutContainer {
    height: 115vh;
    color: darkred;
    background-image: url("../images/bg-img.jpg");
    background-repeat: no-repeat;
}

.firstTitle {
    padding-top: 64px;
}

.firstTitleH {
    text-align: left;
    position: relative;
    padding-left: 2vw;
}

.hTLink {
    height: 20px;
}
.descriptions {
    display: flex;
}

article {
    margin-left: 5vw;
    margin-right: 5vw;
    font-size: 1.2rem;
  }

  @media (min-width: 1100px) {
    article {
      font-size: 1.8rem;
    }
  }


.descriptionLinks {
    height: 50px;
    width: 50px;
    padding: 5px;
    border: solid 3px black;
    border-radius: 20%;
    margin: 40px;
}

.descriptionLinks:hover {
    background-color: blueviolet;
}