h2 {
    color:#51EBA5;
}
.mainAnimation{
    animation-duration: 10s;
    animation-name: infoIn;
    opacity: 1;
    }
      @keyframes infoIn {
          from {
              opacity: 0;
          }
          80% {
            opacity: 0;
            }
          to {
              opacity: 1;
          }
      }

.mainContainer {
    background-color: #282c34;
    color: #e8cd33;
}
