.mobileLetters {
    text-align: center;
    position: relative;
    font-family: 'Varela Round', sans-serif;
}

.navBox1{
    background-color: #282c34;
    color: #e8cd33;
    height: 100%;
    background-image: url("../images/grass-nav-menu-f.png");
    background-position: bottom;
    background-size: 600px;
    background-repeat: no-repeat;
}


.navLogo{
    padding-left: 5px;
}

.navItemsFrame {
    display: inline-block;
    position: relative;
    color: #0087ca;
}
/*#0087ca
or
  #e4000f ???
*/
.navItemsFrame:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #0087ca;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }

.navItemsFrame:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }